}

.navbar {
  background-color: #CA4A00;
  width: 100%;
  border-radius: 0px;
  /* padding: 1em 1em 0em 1em;  */
  margin-bottom: 0em; 
}

ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
}

li {
    float: left;
}

li a {
    display: block;
    color: white;
    text-align: center;
    padding: 0px 15px;
    text-decoration: none;
}

/* Change the link color to #111 (black) on hover */
li a:hover {
    text-decoration: underline;
    color: #eee;
}



